import React from 'react'
import { Link } from 'react-router-dom'
import Page from '../../Page'

import LeftBack from '../../../img/3.png'


function ExperincesPage() {
    return(
        <Page 
            subPageBack="/services"  
            Heading="Experinces"  
            BtnTxt="/getaquote/experiences"
            back={LeftBack}
          >  
            <ul className="blists xslist">
                <li><h1><Link to="/experiences/citycountry">City/Country</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/experiences/tours">Tours</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
            </ul>
        </Page>
    )
}

export default ExperincesPage;