import React from "react";
import { Link } from "react-router-dom";
import Page from "../Page";
import LeftBack from "../../img/2.png";

import "./style.css";

import icon from "../../img/password.svg";
import icon1 from "../../img/1.svg";
import icon2 from "../../img/2.svg";
import FormBottom from "../getintouch/FormBottom";

const Signup = (props) => {
  return (
    <Page pageBack="/" Heading="Sign up" back={LeftBack}>
      <form className="loginform signup">
        <h5>Sign up</h5>
        <div className="inplabel">
          <label>
            <input type="text" placeholder="First name" />
            <img src={icon1} alt="" />
          </label>
          <label>
            <input type="text" placeholder="last name" />
            <img src={icon1} alt="" />
          </label>
        </div>
        <div className="inplabel">
          <label>
            <input type="text" placeholder="User name" />
            <img src={icon1} alt="" />
          </label>
          <label>
            <input type="text" placeholder="Email" />
            <img src={icon2} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label>
            <input type="password" placeholder="Password" />
            <img src={icon} className="pass" alt="" />
          </label>
          <label>
            <input type="password" placeholder="Retype Password" />
            <img src={icon} className="pass" alt="" />
          </label>
        </div>

        <FormBottom />
        <p>
          Already have an account ? <Link to="/login">Login In</Link>
        </p>
      </form>
    </Page>
  );
};

export default Signup;
