import React from "react";

import { Link } from "react-router-dom";
import classNames from "classnames";
import PropTypes from "prop-types";

const QuoteHeaderBtns = (props) => {
  const { activeState = "Fly" } = props;

  return (
    <div className="formPages">
      <Link
        to="/getaquote/flight"
        className={classNames("theme-btn", { active: activeState === "Fly" })}
      >
        Fly
      </Link>
      <Link
        to="/getaquote/hotel"
        className={classNames("theme-btn", { active: activeState === "Stay" })}
      >
        Stay
      </Link>
      <Link
        to="/getaquote/experiences"
        className={classNames("theme-btn", { active: activeState === "Exp" })}
      >
        Experiences
      </Link>
    </div>
  );
};

QuoteHeaderBtns.propTypes = {
  activeState: PropTypes.oneOf(["Fly", "Stay", "Exp"]),
};
export default QuoteHeaderBtns;
