import React from 'react'
import { Link } from 'react-router-dom'
import Page from '../../Page'

import LeftBack from '../../../img/3.png'


function HotelPage() {
    return(
        <Page 
            subPageBack="/services"
            Heading="Hotels"  
            BtnTxt="/getaquote/hotel"
            back={LeftBack}
          >  
            <ul className="blists xslist">
                <li><h1><Link to="/hotel/city">CITY</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/hotel/luxuryhotels">LUXURY HOTELS</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/hotel/hotelsincity">HOTELS IN CITY</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/hotel/hotelpage">HOTEL PAGE</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/hotel/luxuryvillas">LUXURY VILLAS</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/hotel/villapage">VILLA PAGE</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
            </ul>
        </Page>
    )
}






export default HotelPage;