import Page from '../../Page' 
// import { Link } from 'react-router-dom'
import LeftBack from '../../../img/5.png'

/* eslint-disable jsx-a11y/anchor-is-valid */

function BusinessClass() {
    return(
        <Page 
            subPageBack="/flights"
            Heading="business class"  
            BtnTxt="/getaquote/flight"
            back={LeftBack}
          > 
            <ul className="blists xslist">
                <li><h1><a href="#">exclusive upgrades</a></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><a href="#">international departures</a></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><a href="#">around the world</a></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
            </ul>
        </Page>
    )
}

export default BusinessClass;