import classNames from "classnames";
import React from "react";
import style from "./FormStyle.module.css";

export const Dropdown = (props) => {
  const { passDt, SelectName } = props;

  return (
    <>
      <div
        className={classNames("dropdown-container", style.simpleSelectFormContainer)}
      >
        <select className={classNames(style.simpleSelectForm)} name={SelectName}>
          
          {passDt.map((value, i) => (
            <option className={classNames(style.simpleSelectFormOption)} key={i}>
              {value}
            </option>
          ))}
        </select>
      </div>
    </>
  );
};

const SelectOption = (props) => {
  const { theOptValue } = props;

  return (
    <>
      {theOptValue && <Dropdown SelectName={props.SelectName} passDt={theOptValue} />}
    </>
  );
};

export default SelectOption;
