import React from "react";
import { Link } from "react-router-dom";
import Page from "../Page";
import LeftBack from "../../img/2.png";

import "./style.css";

import icon from "../../img/password.svg";
import icon1 from "../../img/1.svg";

const Signin = (props) => {
  return (
    <Page pageBack="/" Heading="Sign In" back={LeftBack}>
      <form className="loginform maillogin">
        <h5>Sign In</h5>
        <label>
          <input type="text" placeholder="User name" />
          <img src={icon1} alt="" />
        </label>
        <label>
          <input type="password" placeholder="Your Password" />
          <img src={icon} className="pass" alt="" />
        </label>

        <label htmlFor="ckb2" className="ckb">
          <input type="checkbox" id="ckb2" />
          <span className="checkmark"></span>
          Remember me
        </label>
        <button type="submit" className="theme-btn">
          Submit
        </button>
        <p>
          Forgot password? <Link to="/password-reset">Click Here</Link>
        </p>
        <p>
          Don't have an account ? <Link to="/signup">Sign up</Link>
        </p>
      </form>
    </Page>
  );
};

export default Signin;
