import React from "react";
import { AnimatePresence } from "framer-motion";
// import {AnimatePresence, motion} from 'framer-motion';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "@popperjs/core/dist/cjs/popper";
import "./Reset.css";

// Home
import Home from "./component/home/Home";
// About us
import About from "./component/about/About";
// Services
import Services from "./component/services/Services";
// Contact
import Contact from "./component/contact/Contact";

// Flights
import Flights from "./component/flights/Flights";
//Flight Classes
import FirstClass from "./component/flights/classes/First";
import BusinessClass from "./component/flights/classes/Business";
import PremiumClass from "./component/flights/classes/premium/Premium";
// Flights extra features for Premium Class
import PricateJets from "./component/flights/classes/premium/private";
import International from "./component/flights/classes/premium/International";
import Northamerica from "./component/flights/classes/premium/Northamerica";

// Hotels
import HotelPage from "./component/flights/hotels/Hotel";
import City from "./component/flights/hotels/City";
import Hotelpage from "./component/flights/hotels/Hotelpage";
import Hotelsincity from "./component/flights/hotels/Hotelsincity";
import LuxuryHotels from "./component/flights/hotels/Luxuryhotels";
import Luxuryvillas from "./component/flights/hotels/Luxuryvillas";
import Villapage from "./component/flights/hotels/Villapage";

// Exprinces
import ExperincesPage from "./component/flights/experiences/Experinces";
import CityCountry from "./component/flights/experiences/CityCountry";
import Tours from "./component/flights/experiences/Tours";

// Get in touch
import GetFlights from "./component/getintouch/flights/Flight";
import Experiences from "./component/getintouch/experiences/Experiences";
import Hotel from "./component/getintouch/hotel/Hotel";

// Blog
import Blog from "./component/blog/Blog";
// Blog Single page
import BlogSinglePage from "./component/blog/BlogSingle";
// Cookie
import Cookie from "./component/cookie/Cookie";
// Privacy
import Privacy from "./component/privacy/Privacy";

// SignIn
import Signin from "./component/signin-signup/Signin";
// Sign-Up
import Signup from "./component/signin-signup/Signup";
// Password-Reset
import PasswordReset from "./component/signin-signup/PassReset";
// NewsLatter
import Newslatter from "./component/newsletter/Newslatter";

const App = (props) => {
  return (
    <BrowserRouter>
      <AnimatePresence>
        <Switch>
          {/* The Main pages */}
          <Route exact path="/" component={Home} />
          <Route path="/home" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/services" component={Services} />
          <Route path="/contact" component={Contact} />
          <Route exact path="/blog" component={Blog} />
          <Route exact path="/blog/singlepage" component={BlogSinglePage} />

          {/* Flishts */}
          <Route exact path="/flights" component={Flights} />
          <Route exact path="/flights/firstclass" component={FirstClass} />
          <Route
            exact
            path="/flights/businessclass"
            component={BusinessClass}
          />
          <Route exact path="/flights/premiumclass" component={PremiumClass} />
          {/* Premium */}
          <Route
            exact
            path="/flights/premiumclass/pricateJets"
            component={PricateJets}
          />
          <Route
            exact
            path="/flights/premiumclass/international"
            component={International}
          />
          <Route
            exact
            path="/flights/premiumclass/northamerica"
            component={Northamerica}
          />

          {/* Hotel pages */}
          <Route exact path="/hotel" component={HotelPage} />
          <Route exact path="/hotel/city" component={City} />
          <Route exact path="/hotel/hotelpage" component={Hotelpage} />
          <Route exact path="/hotel/hotelsincity" component={Hotelsincity} />
          <Route exact path="/hotel/luxuryhotels" component={LuxuryHotels} />
          <Route exact path="/hotel/luxuryvillas" component={Luxuryvillas} />
          <Route exact path="/hotel/villapage" component={Villapage} />

          {/* Experinces */}
          <Route exact path="/experiences" component={ExperincesPage} />
          <Route
            exact
            path="/experiences/citycountry"
            component={CityCountry}
          />
          <Route exact path="/experiences/tours" component={Tours} />

          {/* Get in Touch */}
          <Route exact path="/getaquote/flight" component={GetFlights} />
          <Route exact path="/getaquote/hotel" component={Hotel} />
          <Route exact path="/getaquote/experiences" component={Experiences} />

          <Route exact path="/privacy" component={Privacy} />
          <Route exact path="/cookie" component={Cookie} />
          <Route exact path="/newslatter" component={Newslatter} />
          <Route exact path="/login" component={Signin} />
          <Route exact path="/signup" component={Signup} />
          <Route exact path="/password-reset" component={PasswordReset} />
        </Switch>
      </AnimatePresence>
    </BrowserRouter>
  );
};

export default App;
