import Page from '../../../Page' 
import { Link } from 'react-router-dom'
import LeftBack from '../../../../img/6.png'


function PremiumClass() {
    return(
        <Page 
            subPageBack="/flights"
            Heading="premium economy"  
            BtnTxt="/getaquote/flight"
            back={LeftBack}
          > 
            <ul className="blists xslist">
                <li><h1><Link to="/flights/premiumclass/pricatejets">Pricate Jets</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/flights/premiumclass/international">International</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/flights/premiumclass/northamerica">North America</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
            </ul>
        </Page>
    )
}

export default PremiumClass;