import { Link } from "react-router-dom";

const BlogBlk = (props) => {
  return (
    <div className="blog-blk">
      <h5>
        <Link to={props.pathItis}>{props.blogheading}</Link>
      </h5>
      <p>
        <Link to={props.pathItis}>{props.ShortCpntant}</Link>
      </p>
      <Link to={props.pathItis}>
        <img src={props.blogImg} alt="" />
      </Link>
    </div>
  );
};

export default BlogBlk;
