import React from "react";
import Page from "../Page";
import LeftBack from "../../img/2.png";

import "../signin-signup/style.css";

import icon1 from "../../img/2.svg";

const Newslatter = () => {
  return (
    <Page pageBack="/" Heading="newslatter" back={LeftBack}>
      <form className="loginform newslatter">
        <h5>newslatter</h5>
        <label>
          <input type="text" placeholder="Your Email" />
          <img src={icon1} alt="" />
        </label>

        <button type="button" className="theme-btn">
          Subscribe
        </button>
      </form>
    </Page>
  );
};

export default Newslatter;
