import React, { useRef, useEffect, useState } from "react";
import ReactDOM from "react-dom";
import App from "./App";

// Password Production ,,,, if you don't want this then just remove it this function from here and remove <PssPro /> the tag from bellow
const PssPro = () => {
  const inputFocus = useRef();
  const [PasswordError, setPasswordError] = useState("");
  const [passwordValue, setpasswordValue] = useState("");

  useEffect(() => {
    inputFocus.current.focus();
    return setPasswordError("");
  }, []);

  if (passwordValue === "123trvs")
    document.querySelector("body").classList.add("openPlease");

  const Submit = () => {
    let theAPP = document.querySelector("body");
    let PassInp = document.querySelector("#passtest");
    if (PassInp.value === "123trvs") {
      theAPP.classList.add("openPlease");
    } else {
      setPasswordError("Please Check your Password");
    }
  };

  return (
    <div className="passPro">
      <div className="passes">
        {PasswordError && <p>{PasswordError}</p>}
        <input
          ref={inputFocus}
          onKeyDown={(e) => e.key === "Enter" && Submit()}
          type="password"
          onChange={(e) => setpasswordValue(e.target.value)}
          id="passtest"
          placeholder="type pass"
        />
      </div>
    </div>
  );
};

ReactDOM.render(
  <React.StrictMode>
    <App />
    <PssPro />
  </React.StrictMode>,
  document.getElementById("root")
);
