import React from 'react'
import { Link } from 'react-router-dom'
import Page from '../Page' 

import LeftBack from '../../img/4.png'


function Flights() {
    return(
        <Page 
            subPageBack="/services"  
            Heading="flights"  
            back={LeftBack}
            BtnTxt="/getaquote/flight"
          > 
            <ul className="blists mdlist">
                <li><h1><Link to="/flights/firstclass">First class</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/flights/businessclass">business class</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
                <li><h1><Link to="/flights/premiumclass">premium economy</Link></h1> <p>Ensuring you are comfortable and well rested, First Class cabins will transform your journey into the ultimate luxury experience. </p></li>
            </ul>
        </Page>
    )
}

export default Flights;