import React from 'react'; 
import { motion} from 'framer-motion';
// import {AnimatePresence, motion} from 'framer-motion';
import { Link } from 'react-router-dom';
import Logo from './Logo';
import Back from '../../img/1.png';
import './Home.css';


class Home extends React.Component{
   
    menuBar = (e) => { 
        this.menuIcon = document.querySelector('.menuIcon')
        this.menuIcon.classList.toggle('humbargerAni')
        this.menu = document.querySelector('.menu')
        this.menu.classList.toggle('menuShow')
    }


    render(){
        return(
            <motion.div className="home" 
                animate={{ opacity: 1, scale:1 }}
                transition={{ duration: 0.5 }} 
            >
                <img className="background" src={Back} alt=""/>
                <div className="vBack">  
                    {/* <video loop="" muted="" autoPlay="">
                        <source type="video/mp4" src="https://flixels.s3.amazonaws.com/flixel/ct08dlpwq6eqp8yp1r6v.hd.mp4" />
                        <source type="video/webm" src="http://blucube.net/stuff/codepen-assets/html5vid/beepl-pink-vynil.webm" />
                        <source type="video/ogg" src="http://blucube.net/stuff/codepen-assets/html5vid/beepl-pink-vynil.ogv" />
                    </video> */}
                </div>
                <button type="button" className="menuIcon" onClick={this.menuBar}> <span></span> </button>
                <Logo />
                <div className="home-wrp">
                    <h1>Travel seller</h1>
                    <h5>creating unforgettable journeys</h5>
                </div>
                <div className="menu">
                    <ul>
                        <li><Link to="/about">about</Link></li>
                        <li><Link to="/services">services</Link></li>
                        <li><Link to="/contact">contact</Link></li>
                        <li><Link to="/getaquote/flight">Get a quote</Link></li>
                        <li><Link to="/blog">Blog</Link></li>
                    </ul>
                </div>

                <nav>
                    <ul>
                        <li> 
                            <Link to="/privacy">Privacy</Link>
                            <Link to="/cookie">Cookie</Link>
                            <Link to="/newslatter">Newsletter</Link>
                            <Link to="/login">Login</Link>
                            <Link to="/signup">Sign up</Link>
                            <Link to="/contact">Contact</Link>
                        </li>
                    </ul>
                </nav>
            </motion.div>
        )
    }
}


export default Home; 






