import React from "react";
import Page from "../../Page";
import "../form.css";

import FormBottom from "../FormBottom";
import SimpleSelect from "../Components/SimpleSelect";
import Date from "../Date";

import LeftBack from "../../../img/9.png";
import icon1 from "../../../img/1.svg";
import icon2 from "../../../img/2.svg";
import icon3 from "../../../img/3.svg";
import icon4 from "../../../img/4.svg";
// import icon5 from '../../../img/5.svg'
import icon6 from "../../../img/6.svg";
import icon7 from "../../../img/7.svg";

// Added By vjTravelSeller
import { sendHotelEmail } from "../Components/mailjs_service/SendFormToGmail";
import QuoteHeaderBtns from "../Components/QuoteHeaderBtns";
import { TextAreaAutoHeightIncrease } from "../../../utils/Functions/TextareaSizing";

const theValue = ["trip type", "round trip", "one-way", "multi-city"];
const theValue1 = [
  "No. of person/s",
  "01",
  "02",
  "03",
  "04",
  "05",
  "06",
  "07",
  "08",
  "09",
  "10",
];
const theValue2 = [
  "preferred hotel/s",
  "CITY",
  "LUXURY HOTELS ",
  "HOTELS IN CITY ",
  "HOTEL PAGE ",
  "LUXURY VILLAS ",
  "VILLA PAGE",
];

const Hotel = () => {
  const submitForm = (e) => {
    e.preventDefault();
    sendHotelEmail(e);
  };

  return (
    <Page
      pageBack="/"
      Heading="Travel seller"
      wrp="creating unforgettable journeys"
      back={LeftBack}
      rightHeading="get in touch"
    >
      {/* Top3 Btns  */}
      <QuoteHeaderBtns activeState="Stay" />

      <form onSubmit={(e) => submitForm(e)}>
        <div className="inplabel">
          <label htmlFor="name">
            <input type="text" name="name" placeholder="name" />
            <img src={icon1} alt="" />
          </label>

          <label htmlFor="email">
            <input type="text" name="email" placeholder="email" />
            <img src={icon2} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label htmlFor="phn">
            <input
              type="text"
              name="phoneNumber"
              placeholder="Phone"
              id="phn"
            />
            <img src={icon3} alt="" />
          </label>

          <label htmlFor="trp">
            <SimpleSelect
              theOptValue={theValue}
              SelectName="hotelTripTypeOption"
            />
            <img src={icon4} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label htmlFor="StartDate">
            <Date name="StartDate" plcText="check in date" />
            <img src={icon6} alt="" />
          </label>
          <label htmlFor="EndDate">
            <Date name="EndDate" plcText="check out date" />
            <img src={icon6} alt="" />
          </label>
        </div>
        <div className="inplabel">
          <label>
            <SimpleSelect
              SelectName="hotelTotalPersonOption"
              theOptValue={theValue1}
            />
            <img src={icon1} alt="" />
          </label>

          <label>
            <SimpleSelect
              SelectName="hotelPreferedOption"
              theOptValue={theValue2}
            />
            <img src={icon4} alt="" />
          </label>
        </div>
        <div className="inplabel msgbx">
          <label htmlFor="message">
            <textarea
              id="message"
              name="CommentText"
              style={{ resize: "none",overflow: "hidden" }}
              onChange={(e) => TextAreaAutoHeightIncrease(e)}
              placeholder="question or comment"
            ></textarea>
            <img src={icon7} alt="" />
          </label>
        </div>

        <FormBottom />
      </form>
    </Page>
  );
};
