import React from "react";
import Page from "../../Page";
import "../form.css";

import SimpleSelect from "../Components/SimpleSelect";

import FormBottom from "../FormBottom";
import AutoComplateItis from "../AutoComplateDats";
import Date from "../Date";

import LeftBack from "../../../img/10.png";
import icon1 from "../../../img/1.svg";
import icon2 from "../../../img/2.svg";
import icon3 from "../../../img/3.svg";
import icon4 from "../../../img/4.svg";
import icon5 from "../../../img/5.svg";
import icon6 from "../../../img/6.svg";
import icon7 from "../../../img/7.svg";

// Added By vjTravelSeller
import { sendFlightsEmail } from "../Components/mailjs_service/SendFormToGmail";
import QuoteHeaderBtns from "../Components/QuoteHeaderBtns";
import { TextAreaAutoHeightIncrease } from "../../../utils/Functions/TextareaSizing";

const theValue = ["trip type", "round trip", "one-way", "multi-city"];
const theValue1 = [
  "cabin class",
  "first class",
  "business class",
  "premium economy",
];
const theValue2 = [
  "preferred airline / alliance",
  "preferred / alliance",
  " airline / alliance",
  "preferred airline",
];

const GetFlights = (props) => {
  const submitForm = (props) => {
    console.log("submitForm ~ props", props);
    sendFlightsEmail(props);
  };

  return (
    <Page
      pageBack="/"
      Heading="Travel seller"
      wrp="creating unforgettable journeys"
      back={LeftBack}
      rightHeading="get in touch"
    >
      {/* Top3 Btns  */}

      <QuoteHeaderBtns activeState="Fly" />

      <form onSubmit={(data) => submitForm(data)}>
        <div className="inplabel">
          <label htmlFor="name">
            <input type="text" name="name" placeholder="name" />
            <img src={icon1} alt="" />
          </label>

          <label htmlFor="email">
            <input type="text" placeholder="email" name="email" id="email" />
            <img src={icon2} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label htmlFor="phn">
            <input
              type="text"
              name="phoneNumber"
              placeholder="Phone"
              id="phn"
            />
            <img src={icon3} alt="" />
          </label>

          <label htmlFor="trp">
            <SimpleSelect
              theOptValue={theValue}
              SelectName="flightTripTypeOption"
            />
            <img src={icon4} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label htmlFor="liveFrom">
            <AutoComplateItis
              Name="LeavingFrom"
              placeholdertext="leaving from"
            />
            <img src={icon5} alt="" />
          </label>
          <label htmlFor="to" className="autoComp">
            <AutoComplateItis Name="GoingTo" placeholdertext="going to" />
            <img src={icon5} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label htmlFor="StartDate">
            <Date name="StartDate" plcText="departure date" />
            <img src={icon6} alt="" />
          </label>

          <label htmlFor="EndDate">
            <Date name="EndDate" plcText="return date" />
            <img src={icon6} alt="" />
          </label>
        </div>

        <div className="inplabel">
          <label>
            <SimpleSelect
              theOptValue={theValue1}
              SelectName="flightCabinClassOption"
            />
            <img src={icon4} alt="" />
          </label>
          <label>
            <SimpleSelect
              theOptValue={theValue2}
              SelectName="flighPreferedOption"
            />
            <img src={icon4} alt="" />
          </label>
        </div>

        <div className="inplabel msgbx">
          <label htmlFor="message">
            <textarea
              id="message"
              name="CommentText"
              style={{ resize: "none", overflow: "hidden" }}
              onChange={(e) => TextAreaAutoHeightIncrease(e)}
              placeholder="question or comment"
            ></textarea>
            <img src={icon7} alt="" />
          </label>
        </div>

        <FormBottom />
      </form>
    </Page>
  );
};

export default GetFlights;
