import React from "react";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";
import Logo from "../component/home/Logo";
import Ximage from "../img/x.png";
import "./page.css";

const Page = (props) => {
  const {
    Heading,
    wrp,
    back,
    rightHeading,
    rightWrp,
    pageBack,
    subPageBack,
    btnTxt,
    children,
  } = props;

  return (
    <motion.div
      className="page"
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5 }}
    >
      {subPageBack && (
        <Link to={subPageBack} className="closeSubPage">
          <img src={Ximage} alt="" />
        </Link>
      )}
      <div className="page-left">
        <div className="left-page-wrp">
          <div className="leftback">
            <img src={back} alt="" />
          </div>
          <Logo />
          {pageBack && (
            <Link to={pageBack} className="closePage">
              Close <img src={Ximage} alt="" />
            </Link>
          )}
          <div className="page-left-contents">
            <h1>{Heading}</h1>
            <h5>{wrp}</h5>
            {btnTxt && (
              <Link to={btnTxt} className="theme-btn">
                get in touch
              </Link>
            )}
          </div>
        </div>
      </div>
      <div className="page-right">
        <div className="wrp-inner">
          {rightHeading && <h5>{rightHeading}</h5>}
          {rightWrp && <p>{rightWrp}</p>}
          {children}
        </div>

        <div className="page-bottom-menu">
          <Link to="/newslatter">Newsletter</Link>
          <Link className="lastMenu" to="/contact">
            Contact
          </Link>
        </div>
      </div>
    </motion.div>
  );
};

export default Page;
