import React from "react";
// import { Link } from 'react-router-dom'
import Page from "../Page";
import LeftBack from "../../img/2.png";

import "./style.css";

import icon from "../../img/password.svg";


const PasswordReset = (props) => {
  return (
    <Page pageBack="/" Heading="Password reset" back={LeftBack}>
      <form className="loginform maillogin passwordreset">
        <h5>Password reset</h5>
        <label>
          <input type="text" placeholder="Old Password" />
          <img src={icon} className="pass" alt="" />
        </label>
        <label>
          <input type="password" placeholder="New Password" />
          <img src={icon} className="pass" alt="" />
        </label>
        <label>
          <input type="password" placeholder="Retype Password" />
          <img src={icon} className="pass" alt="" />
        </label>
        <button type="submit" className="theme-btn mt-4">
          Submit
        </button>
      </form>
    </Page>
  );
};

export default PasswordReset;
