// Using Emailjs To send Form
import emailjs from "emailjs-com";

export const sendFlightsEmail = (e) => {
  e.preventDefault();
  console.log("sendFlightsEmail", e.target);

  /** Email Variables Incuded in form - Flights
   * name
   * email
   * phoneNumber
   *
   * StartDate
   * EndDate
   *
   * {{GoingTo}}
   * {{LeavingFrom}}
   *
   * Options-[{{flightTripTypeOption}}, {{flightCabinClassOption}}, {{flightPreferedOption}}]
   * CommentText
   */

  emailjs
    .sendForm(
      "vjTravelSellerMail",
      "Flight_001",
      e.target,
      "user_f4yhd0HbxzpVh9n4BTISC"
    )
    .then(
      (result) => {
        console.log(result);
      },
      (error) => {
        console.log(error);
      }
    );
};

